* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  background-color: #4440f6;
  justify-content: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.submit {
  cursor: pointer;
  letter-spacing: .04em;
  background-color: #c8c8e9;
  border: 0;
  border-radius: 4px;
  padding: 10px;
}

.submit:hover, .submit:focus {
  color: #fff;
  background-color: #6967fb;
}

.gallery {
  background-color: #fafafa;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border: 1px solid;
  border-radius: 4px;
  box-shadow: 4px 2px 2px #999696;
}

.info {
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  display: flex;
}

.gallery__link {
  color: inherit;
  text-decoration: none;
}

.img {
  width: 300px;
  height: 200px;
  display: block;
}

.load-more {
  cursor: pointer;
  color: #fff;
  letter-spacing: .04em;
  background-color: #4440f6;
  border: 0;
  border-radius: 4px;
  margin: 40px auto;
  padding: 10px;
}

.load-more:hover, .load-more:focus {
  background-color: #2927a2;
}

